/* Global style of tags */
html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "SFProDisplay", "Times New Roman", sans-serif;
  font-weight: 400;
  font-size: 18px;
  background-color: #fff;
}

a {
  text-decoration: none;
  color: #be1622;
}
a:hover {
  color: #ae1622;
}
a:active {
  color: #8e1622;
}

img {
  max-width: 100%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Block styles */
.container {
  margin: 0 auto;
  width: 320px;
  height: auto;
}
@media (min-width: 768px) {
  .container {
    width: 640px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1320px;
  }
}

.header {
  margin: 0;
  padding: 0;
}
.header--1 {
  font-size: 32px;
  font-weight: 700;
  color: #be1622;
}
.header--2 {
  font-size: 28px;
  font-weight: 500;
}

.button {
  display: flex;
  align-items: center;
  padding: 5px 20px;
  background-color: #be1622;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
.button:hover {
  background-color: #ae1622;
}
.button:active {
  background-color: #8e1622;
}

.button__icon {
  margin-right: 10px;
}

.button__icon path {
  fill: #ffffff;
}

.standart-block {
  padding: 40px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.02);
}

.page {
  display: flex;
  justify-content: start;
  margin: 0 auto;
  width: 1920px;
}

.page__menu {
  flex: 0 1 310px;
  width: 310px;
}

.page__content {
  flex: 1 0 auto;
  padding: 20px;
}

.page-content {
  background-color: #fbfbfb;
}

.page-content__header {
  display: flex;
  margin-bottom: 20px;
  padding: 25px 40px;
}

.page-content__button {
  margin-left: auto;
}

.menu {
  height: 100vh;
}

.menu__inner {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  height: 100%;
  padding-top: 50px;
  padding-right: 50px;
  padding-left: 50px;
  padding-bottom: 50px;
}

.menu__account {
  position: absolute;
  bottom: 50px;
}

.logo--menu {
  margin-bottom: 75px;
}

.menu-list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 24px;
}

.menu-list__item {
  display: flex;
  align-items: center;
}
.menu-list__item--active {
  font-weight: 500;
}
.menu-list__item--active .menu-list__icon path {
  fill: #be1622;
}
.menu-list__item--active .menu-list__link {
  color: #be1622;
}
.menu-list__item:hover .menu-list__icon path {
  fill: #be1622;
  transition: all 0.5s 0s ease;
}
.menu-list__item:hover .menu-list__link {
  color: #be1622;
  transition: all 0.5s 0s ease;
}

.menu-list__icon-block {
  display: flex;
  margin-right: 23px;
}

.account__inner {
  display: flex;
  align-items: center;
}

.account__logo {
  margin-right: 19px;
}

.account__name {
  font-weight: 500;
}