/* Global style of tags */

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;

  font-family: "SFProDisplay", "Times New Roman", sans-serif;
  font-weight: 400;
  font-size: 18px;

  background-color: #fff;
}

a {
  text-decoration: none;
  color: $main-color;

  &:hover {
    color: $main-color-hover;
  }

  &:active {
    color: $main-color-active;
  }
}

img {
  max-width: 100%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

