.button {
  display: flex;
  align-items: center;
  padding: 5px 20px;

  background-color: $main-color;

  color: $basic-white;

  border: none;
  border-radius: 10px;

  cursor: pointer;

  &:hover {
    background-color: $main-color-hover;
  }

  &:active {
    background-color: $main-color-active;
  }
}

.button__icon {
  margin-right: 10px;
}

.button__icon path {
  fill: $basic-white;
}
