.menu-list {
  @include clear-list();

  font-size: 24px;
}

.menu-list__item {
  display: flex;
  align-items: center;

  &--active {
    font-weight: 500;

    .menu-list__icon path {
      fill: $main-color;
    }

    .menu-list__link {
      color: $main-color;
    }
  }

  &:hover {
    .menu-list__icon path {
      fill: $main-color;

      transition: all 0.5s 0s ease;
    }

    .menu-list__link {
      color: $main-color;

      transition: all 0.5s 0s ease;
    }
  }
}

.menu-list__icon-block {
  display: flex;

  margin-right: 23px;
}
